body {
  
  margin: 0;
  padding: 0;
  font-family: "Courier New", Courier, monospace;
  background: -webkit-linear-gradient(1deg, #e66465, #9198e5);
  background: linear-gradient(89deg, #e66465, #9198e5);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

button {
  cursor: pointer;
}

.App {
  text-align: center;
}
.center {
  display: flex;
  justify-content: center;
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}
.button {
  padding: 0px;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}

.button:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}
.form {
  width: 1200px;

  background: -webkit-radial-gradient(
        0% 50%,
        circle farthest-side,
        #fb1 23.5%,
        rgba(240, 166, 17, 0) 0
      )
      21px 30px,
    -webkit-radial-gradient(
        0% 50%,
        circle farthest-side,
        #b71 24%,
        rgba(240, 166, 17, 0) 0
      )
      19px 30px,
    -webkit-linear-gradient(
        #fb1 14%,
        rgba(240, 166, 17, 0) 0,
        rgba(240, 166, 17, 0) 85%,
        #fb1 0
      )
      0 0,
    -webkit-linear-gradient(
        300deg,
        #fb1 24%,
        #b71 0,
        #b71 26%,
        rgba(240, 166, 17, 0) 0,
        rgba(240, 166, 17, 0) 74%,
        #b71 0,
        #b71 76%,
        #fb1 0
      )
      0 0,
    -webkit-linear-gradient(
        60deg,
        #fb1 24%,
        #b71 0,
        #b71 26%,
        rgba(240, 166, 17, 0) 0,
        rgba(240, 166, 17, 0) 74%,
        #b71 0,
        #b71 76%,
        #fb1 0
      )
      0 0,
    -webkit-linear-gradient(left, #b71 2%, #fb1 0, #fb1 98%, #b71 0%) 0 0 #fb1;

  background: radial-gradient(
        circle farthest-side at 0% 50%,
        #fb1 23.5%,
        rgba(240, 166, 17, 0) 0
      )
      21px 30px,
    radial-gradient(
        circle farthest-side at 0% 50%,
        #b71 24%,
        rgba(240, 166, 17, 0) 0
      )
      19px 30px,
    linear-gradient(
        #fb1 14%,
        rgba(240, 166, 17, 0) 0,
        rgba(240, 166, 17, 0) 85%,
        #fb1 0
      )
      0 0,
    linear-gradient(
        150deg,
        #fb1 24%,
        #b71 0,
        #b71 26%,
        rgba(240, 166, 17, 0) 0,
        rgba(240, 166, 17, 0) 74%,
        #b71 0,
        #b71 76%,
        #fb1 0
      )
      0 0,
    linear-gradient(
        30deg,
        #fb1 24%,
        #b71 0,
        #b71 26%,
        rgba(240, 166, 17, 0) 0,
        rgba(240, 166, 17, 0) 74%,
        #b71 0,
        #b71 76%,
        #fb1 0
      )
      0 0,
    linear-gradient(90deg, #b71 2%, #fb1 0, #fb1 98%, #b71 0%) 0 0 #fb1;
  background-size: 40px 60px;
}

.bounding-box {
  position: absolute;
  /* box-shadow: inset 0px 0px 0px 3px #f2de14; */
  border: #f2de14 solid 2px;
  /* display: flex;
  flex-wrap: wrap;
  justify-content: center; */
  cursor: pointer;
}

 .Tilt {
 	background: -webkit-linear-gradient(1deg, #e66465, #9198e5);
 	background: linear-gradient(89deg, #e66465, #9198e5); 
 	
 }

